var token
import {
	UPLOADFILE,
	FEEDBACK
} from '../../../utils/api.js'
// import VConsole from '../../../utils/vconsole.min.js'
// var vconsole = new VConsole()
export default {
	data() {
		return {
			imgList: [],
			adviceText: '',
			satisfaction: 4, //满意度
			newList: '',
			imgPath: '',
			uploadPath: '',
			evaluationData: [{
					uncheck: '../../../static/haveAWord/henbuman@2x.png',
					checked: '../../../static/haveAWord/henbuman1@2x.png',
					satisfaction: 0,
					text: '很不满'
				},
				{
					uncheck: '../../../static/haveAWord/buman@2x.png',
					checked: '../../../static/haveAWord/buman1@2x.png',
					satisfaction: 1,
					text: '不满'
				},
				{
					uncheck: '../../../static/haveAWord/yiban@2x.png',
					checked: '../../../static/haveAWord/yiban1@2x.png',
					satisfaction: 2,
					text: '一般'
				},
				{
					uncheck: '../../../static/haveAWord/manyi@2x.png',
					checked: '../../../static/haveAWord/manyi1@2x.png',
					satisfaction: 3,
					text: '满意'
				},
				{
					uncheck: '../../../static/haveAWord/henmanyi1@2x.png',
					checked: '../../../static/haveAWord/henmanyi@2x.png',
					satisfaction: 4,
					text: '很满意'
				}
			]
		}
	},
	onLoad() {
		this.initData()
		token = uni.getStorageSync('myToken')
	},
	methods: {
		// 用户反馈
		feedback() {
			const params = {
				satisfaction: this.satisfaction,
				content: this.adviceText,
				attachArray: this.imgList,
				attach: this.imgList.join(",")
			}
			console.log(params)
			this.$request.url_requestSimple(FEEDBACK, params, 'POST', res => {
				uni.showToast({
					title: '反馈成功！',
					icon: 'success',
					duration: 1200
				})
				setTimeout(() => {
					uni.reLaunch({
						url: '../MinePage'
					})
				}, 1200)
			}, err => {})

		},
		ChooseImage() {
			var _this = this
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album', 'camera '],
				success: (res) => {
					console.log('选择图片', res)
					_this.imgPath = res.tempFilePaths.toString()
					console.log('图片的路径', _this.imgPath)
					uni.getImageInfo({
						src: _this.imgPath,
						success: (res) => {
							console.log('压缩前', res)
							let canvasWidth = res.width //图片原始长宽
							let canvasHeight = res.height
							let img = new Image()
							img.src = res.path
							let canvas = document.createElement('canvas');
							let ctx = canvas.getContext('2d')
							canvas.width = canvasWidth / 4.5
							canvas.height = canvasHeight / 4.5
							ctx.drawImage(img, 0, 0, canvasWidth / 4.5, canvasHeight / 4.5)
							canvas.toBlob(function(fileSrc) {
								_this.uploadPath = window.URL.createObjectURL(fileSrc)
								// 上传图片
								uni.uploadFile({
									url: UPLOADFILE,
									// filePath: res.tempFilePaths[0],
									filePath: _this.uploadPath,
									header: {
										"AI-Chat-Token": token
									},
									name: 'file',
									success: (uploadFileRes) => {
										_this.imgList.push((JSON.parse(uploadFileRes.data).data))
										// _this.imgList = _this.imgList.concat(res.tempFilePaths)
									},
									fail: () => {
										console.log('上传图片的路径',_this.uploadPath)
										uni.showModal({
											content: '图片上传失败，请重试！',
											showCancel: false
										});
									}
								})
								
							})

						}
					})
					// 上传图片
					// uni.uploadFile({
					// 	url: UPLOADFILE,
					// 	// filePath: res.tempFilePaths[0],
					// 	filePath: _this.uploadPath,
					// 	header: {
					// 		"AI-Chat-Token": token
					// 	},
					// 	name: 'file',
					// 	success: (uploadFileRes) => {
					// 		_this.imgList.push((JSON.parse(uploadFileRes.data).data))
					// 		// _this.imgList = _this.imgList.concat(res.tempFilePaths)
					// 	},
					// 	fail: () => {
					// 		console.log('上传图片的路径',_this.uploadPath)
					// 		uni.showModal({
					// 			content: '图片上传失败，请重试！',
					// 			showCancel: false
					// 		});
					// 	}
					// })
					console.log('imglist有数据是', this.imgList)
				}
			})
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			})
		},
		DelImg(e) {
			console.log('delete', e)
			uni.showModal({
				title: '提示',
				content: '确认移除这张图片?',
				cancelText: '再等等',
				confirmText: '确认',
				success: (res) => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			});
		},
		initData() {
			for (let i = 0; i < this.evaluationData.length; i++) {
				this.evaluationData[i].isClick = false
				this.evaluationData[this.evaluationData.length - 1].isClick = true
			}
		},
		choose(index) {
			this.satisfaction = this.evaluationData[index].satisfaction
			for (let i = 0; i < this.evaluationData.length; i++) {
				if (index == i) {
					this.evaluationData[index].isClick = !this.evaluationData[index].isClick
				} else {
					this.evaluationData[i].isClick = false
				}
			}
			this.$forceUpdate()
		},
	}
}
